@import '../../styles/fonts';
@import '../../styles/variables';
@import url('//fonts.googleapis.com/earlyaccess/nanummyeongjo.css');

*{
    margin : 0;
    padding : 0;
    box-sizing : border-box;
}

.container{
    width: 100%;
    height : 640px;
}


.imageWrapper{
    background-image: url('../../../public/MainLayoutImage/mainBackgroundImage.png');
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat; 
    width: 100%;
    height: 640px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}   

.contentWrapper2{
    display: flex;
    width : 1240px;
    height : 637px;
    flex-direction: row;
    justify-content: space-between;
}

.messageWrapper{
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;
    margin-bottom: 115px;
    > div {
        display: flex;
        text-align: center;
        text-shadow:
            -2px -2px 0 #353535,
            0   -2px 0 #353535,
            2px -2px 0 #353535,
            2px  0   0 #353535,
            2px  2px 0 #353535,
            0    2px 0 #353535,
            -2px  2px 0 #353535,
            -2px  0   0 #353535;
        color : white;
        font-family: "Nanum Myeongjo" ,Georgia, 'Times New Roman', Times, serif;
        font-weight: 700;

        
        &:first-of-type {
          font-size: 53px;
        }
    
        &:last-of-type {
          font-family: "Pretendard-Medium", Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
          font-size: 30px;
          margin-top: 20px;
          font-weight: 600;
        }
    }
}


.btnContainer{
    width : 400px;
    height : 310px;
    margin-top: 260px;
}

.btnWrapper{
    display: flex;
    flex-direction: column;
    justify-content: end;
    align-items: center;
    width: 100%;
    gap:20px;
}

.first{
    width : 400px;  
    height : 90px;
    background-color: #4095A2;
    display: flex;
    align-items: center;
}

.iconWrapper{
    background-image : url('../../../public/MainLayoutImage/mainCall.png');
    background-repeat: no-repeat;
    width: 53px;
    height: 50px;
    margin-left: 40px;
}
.iconWrapper2{
    background-image : url('../../../public/MainLayoutImage/mainIcon.png');
    background-repeat: no-repeat;
    width: 53px;
    height: 50px;
    margin-left: 40px;
}
.iconWrapper3{
    background-image : url('../../../public/MainLayoutImage/mainFlower.png');
    background-repeat: no-repeat;
    width: 53px;
    height: 50px;
    margin-left: 40px;
}

.stringWrapper{
    color : white;
    font-size: 18px;
    font-family: 'Pretendard-Regular';
    font-weight: 600;
    margin-left: 25px;
    margin-top : 6px;
    line-height: 20px;
    margin-right: 30px;
}

.comment{
    margin-left: 1px;
    margin-bottom: 2px;
}

.numberWrapper{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.numberIcon{
    background-image : url('../../../public/MainLayoutImage/icon.png');
    background-repeat: no-repeat;
    width: 25px;
    height: 25px;
}


.two{
    width : 400px;
    height : 90px;
    background-color: #775D34;
    margin-left: 20px;
    margin-right: 20px;
    display: flex;
    align-items: center;

}
.three{
    width : 400px;
    height : 90px;
    background-color: #72732C;
    display: flex;
    align-items: center;

}

