// * 캘린더 49제 반응형
.milestoneLabel {
  font-family: "Pretendard-SemiBold";
  color: #4095a2;
  font-size: 14px;

  // * 반응형 제작 *1023)
  @media screen and (max-width: 1023px) {
    font-size: 12px;
  }
}
// * 캘린더 음력 반응형
.lunarDate {
  font-family: "Pretendard-Regular";
  color: #767676;
  font-size: 12px;

  // * 반응형 제작 *1023)
  @media screen and (max-width: 1023px) {
